@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@600&family=Oxygen:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  display: grid;
  grid-template-columns: 5fr 2fr;
  grid-template-rows: 3fr 4fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  position: absolute;
  width: 80%;
  height: 80%;
  top:10%;
  left: 10%;
}
.App1{
  grid-area: 1 / 1 / 3 / 2;
/*  background-image: url("./Images/pexels-brett-sayles-1431822.jpg");*/
    background-size: cover;
   
    background-repeat: no-repeat;
height: 100%;
}

.App2{
  grid-area: 1 / 2 / 2 / 3;
  background: rgb(124,124,133);
  background: linear-gradient(90deg, rgba(124,124,133,1) 0%, rgba(30,30,31,1) 0%, rgba(102,102,102,1) 90%);
}
.App3{
  grid-area: 2 / 2 / 3 / 3;
  background: rgb(124,124,133);
  background: linear-gradient(90deg, rgba(124,124,133,1) 0%, rgba(30,30,31,1) 0%, rgba(102,102,102,1) 90%);
  overflow: scroll;
}
.App3::-webkit-scrollbar {
  display: none;
}

